// Spinner.js
import React, { useEffect, useState } from "react";
import { ClipLoader } from "react-spinners";
import "tailwindcss/tailwind.css";

const quotes = [
  "Joining a club teaches you time management.",
  "Joining a club helps you explore your hidden talents",
  "Club activities are fun to do with friends",
];

const Spinner = () => {
  const [quoteIndex, setQuoteIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setQuoteIndex((prevIndex) => (prevIndex + 1) % quotes.length);
    }, 1500); // Change quote every 3 seconds

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100">
      <div className="text-center">
        <ClipLoader size={150} color={"#123abc"} loading={true} />
        <p className="mt-4 text-lg font-semibold text-gray-700 animate-fade-in">
          {quotes[quoteIndex]}
        </p>
      </div>
    </div>
  );
};

export default Spinner;
