import React, { lazy, Suspense } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Provider, useSelector } from "react-redux";
import store from "./redux/store"; // Ensure the correct path to your store
import Spinner from "./components/Spinner";
import { ProtectedRoute } from "./components/ProtectedRoute";
import { PublicRoute } from "./components/PublicRoute";

// Lazy load the components
const HomePage = lazy(() => import("./pages/HomePage"));
const Login = lazy(() => import("./pages/Login"));
const Register = lazy(() => import("./pages/Register"));
const Home = lazy(() => import("./components/Home"));
const AddClub = lazy(() => import("./pages/admin/AddClub"));
const ViewClub = lazy(() => import("./pages/admin/ViewClub"));
const SingleClub = lazy(() => import("./pages/admin/SingleClub"));
const AllClubs = lazy(() => import("./pages/user/AllClubs"));
const Categories = lazy(() => import("./pages/user/Categories"));
const SingleClubUser = lazy(() => import("./pages/user/SingleClubUser"));
const Applications = lazy(() => import("./pages/admin/Applications"));
const UserApplications = lazy(() => import("./pages/user/UserApplications"));
const About = lazy(() => import("./pages/About"));

function App() {
  const loading = useSelector((state) => state.alerts.loading);

  return (
    <Provider store={store}>
      <BrowserRouter>
        {loading ? (
          <Spinner />
        ) : (
          <Suspense fallback={<Spinner />}>
            <Routes>
              <Route
                path="/"
                element={
                  <ProtectedRoute>
                    <Home />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/about"
                element={
                  <ProtectedRoute>
                    <About />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/add-club"
                element={
                  <ProtectedRoute>
                    <AddClub />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/admin/view-clubs"
                element={
                  <ProtectedRoute>
                    <ViewClub />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/user/clubs"
                element={
                  <ProtectedRoute>
                    <AllClubs />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/user/applications"
                element={
                  <ProtectedRoute>
                    <UserApplications />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/user/categories"
                element={
                  <ProtectedRoute>
                    <Categories />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/admin/applications"
                element={
                  <ProtectedRoute>
                    <Applications />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/admin/club/:id"
                element={
                  <ProtectedRoute>
                    <SingleClub />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/user/club/:id"
                element={
                  <ProtectedRoute>
                    <SingleClubUser />
                  </ProtectedRoute>
                }
              />
              <Route
                path="/login"
                element={
                  <PublicRoute>
                    <Login />
                  </PublicRoute>
                }
              />
              <Route
                path="/register"
                element={
                  <PublicRoute>
                    <Register />
                  </PublicRoute>
                }
              />
            </Routes>
          </Suspense>
        )}
      </BrowserRouter>
    </Provider>
  );
}

export default App;
